<template>
    <div class="body">
        <header_component></header_component>

        <div class="docs-content" :style="contentStyle">
            <slot></slot>
        </div>

        <left_nav :menus="leftNavMenus" v-if="leftNavMenus.length"></left_nav>

        <bottom_nav @setLeftNavMenus="setLeftNavMenus"></bottom_nav>
    </div>
</template>

<script>
    import '@/assets/pc/css/spectre.min.css'
    import '@/assets/pc/css/spectre-exp.min.css'
    import '@/assets/pc/css/spectre-icons.min.css'
    import '@/assets/pc/css/reset.css'

    import header_component from './components/Header'
    import left_nav from './components/Left_nav'
    import bottom_nav from './components/Bottom_nav'

    export default {
        name: "layout",
        components: {header_component, left_nav, bottom_nav},
        data() {
            return {
                leftNavMenus: []
            }
        },
        computed: {
            contentStyle() {
                if (!this.leftNavMenus.length) {
                    return {'padding-left': '25px'}
                }
            }
        },
        methods: {
            setLeftNavMenus(val) {
                if (!val){
                    this.leftNavMenus = [];
                }else{
                    this.leftNavMenus = val;
                }
            }
        }
    };
</script>
