<template>
    <div class="bottom_nav_bg" v-if="list.length">
        <div class="bottom_nav">
            <ul class="rdfwList">
                <template v-for="(l,index) in list">
                    <li v-if="index!==4">
                        <a @click="handleSelect(l,$event)">
                        <span :routeName="l.hasOwnProperty('routeName')?l.routeName:null">
                            <img :src="l.icon" alt="" style="z-index: 10000"/>
                        </span>
                            {{l.name}}
                        </a>
                    </li>

                    <li class="menu_dj" v-else>
                        <img :src="l.icon" alt="" style="z-index: 10000"/>
                    </li>
                </template>
            </ul>
        </div>
    </div>
</template>

<script>
    import {pc} from "@/dictionaries/menu";
    import {logout} from "@/api/user";

    export default {
        data() {
            return {
                list: pc,
            }
        },
        methods: {
            watchRoute(val) {
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].hasOwnProperty('routeName')
                        && this.list[i].routeName === val.name) {
                        let menus = this.list[i].hasOwnProperty('routeName') ? this.list[i].menus : [];
                        this.setCurrent(this.list[i].routeName, menus,1);
                        return;
                    }

                    if (this.list[i].hasOwnProperty('menus')) {
                        let menus_i = this.list[i].menus;
                        for (let j = 0; j < menus_i.length; j++) {
                            if (menus_i[j].routeName === val.name) {
                                this.setCurrent(this.list[i].routeName, menus_i,2);
                                return;
                            }

                            if (menus_i[j].hasOwnProperty('menus')) {
                                let menus_j = menus_i[j].menus;
                                for (let k = 0; k < menus_j.length; k++) {
                                    if (menus_j[k].routeName === val.name) {
                                        this.setCurrent(this.list[i].routeName, menus_i,3);
                                        return;
                                    }
                                }
                            }
                        }
                    }
                }
            },
            setCurrent(routeName, menus,level) {
                if (!menus) {
                    return false;
                }

                let spans = $('.rdfwList span');
                for (let i = 0; i < spans.length; i++) {
                    let spanRouteName = $(spans[i]).attr('routeName');
                    if (routeName === spanRouteName) {
                        $('.rdfwList span').removeClass("current");
                        $(spans[i]).addClass("current");

                        switch (routeName) {
                            case '党建展览':
                                this.$emit('setLeftNavMenus', []);
                                break;
                            default:
                                this.$emit('setLeftNavMenus', menus);
                                break;
                        }
                    }
                }
            },
            handleSelect(li, obj) {
                if (!li.routeName) {
                    this.$info({
                        title: '提示',
                        content: (
                            <div>
                                <p>功能开发中...</p>
                            </div>
                        ),
                    })
                    return;
                }

                if (li.routeName === 'logout') {
                    logout()
                        .then(() => {
                            this.$info({
                                title: '提示',
                                content: (
                                    <div>
                                        <p>登出成功</p>
                                    </div>
                                ),
                            })
                            this.$router.push({name: 'login'});
                        })
                    return;
                }


                const path = obj.path;
                let ele = null;
                for (let i = 0; i < path.length; i++) {
                    if (path[i].tagName === "A") {
                        ele = path[i];
                        break;
                    }
                }

                $('.rdfwList span').removeClass("current");
                $(ele).children("span:first-child").addClass("current");

                this.$router.push({name: li.routeName});

                if (!li.hasOwnProperty('menusShow') || li.menusShow) {
                    this.$emit('setLeftNavMenus', li.menus);
                } else if (li.hasOwnProperty('menusShow') && !li.menusShow) {
                    this.$emit('setLeftNavMenus', []);
                }
            }
        },
        watch: {
            $route: {
                handler(val) {
                    //主要处理后退时候底部按钮的状态
                    this.watchRoute(val);
                }
            }
        },
        mounted() {
            //主要处理刷新时候底部按钮的状态
            this.watchRoute(this.$route);
        }
    }
</script>
