<template>
    <div class="left_nav" v-if="menus_computed.length">
        <nav class="navigation">
            <a class="navigation-item"
               v-for="(l,index) in menus_computed"
               :style="getStyle(index,menus.length)"
               :routeName="l.hasOwnProperty('routeName')?l.routeName:''"
               @click="handleSelect">
                {{l.name}}
            </a>
        </nav>
    </div>
</template>

<script>
    import {pc} from "@/dictionaries/menu";
    import {in_array, empty} from "@/libs/tools";

    export default {
        props: {
            menus: {
                required: true,
                type: Array,
            }
        },
        computed: {
            menus_computed() {
                let m = this.menus;
                let result = [];
                for (let i = 0; i < m.length; i++) {
                    let row = m[i];
                    if (!row.hasOwnProperty('auth') || empty(row.auth)) {
                        result.push(row);
                    } else if (in_array(row.auth, this.$store.getters.getAuths)) {
                        result.push(row);
                    }
                }
                return result;
            },
        },
        methods: {
            getStyle(index, length) {
                if (index + 1 === length) {
                    return {height: '32px'}
                }
            },
            handleSelect(obj) {
                const ele = obj.target;
                let routeName = $(ele).attr("routeName");
                if (!routeName) {
                    this.$info({
                        title: '提示',
                        content: (
                            <div>
                                <p>功能开发中...</p>
                            </div>
                        ),
                    })
                    return;
                }

                $('.navigation a').removeClass("current");
                $(ele).addClass("current");
                this.$router.push({name: routeName});
            },
            setCurrentClass() {
                const eles = $('.navigation a');
                // $(eles[0]).addClass("current");

                for (let i = 0; i < eles.length; i++) {
                    if ($(eles[i]).attr('routeName') === this.$route.name) {
                        $('.navigation a').removeClass("current");
                        $(eles[i]).addClass("current");
                        return;
                    }
                }
            }
        },
        watch: {
            $route: {
                handler(val) {
                    // console.log('route changed:', this.$route, pc);
                    this.setCurrentClass();
                }
            }
        }
    }
</script>
