<template>
    <div>
        <a-tag color="#f50" v-if="is_online===false" @click="handleGoOnline">离线</a-tag>
        <a-tag color="#87d068" v-else>在线</a-tag>
    </div>
</template>

<script>
    import ws from '@/socket'

    export default {
        name: "SocketTip",
        data() {
            return {}
        },
        computed: {
            is_online() {
                return this.$store.state.pc.isOnline;
            }
        },
        methods: {
            init() {
                this.ws = new ws();
                this.ws.init(this.$store.state.common.user_id);
            },
            handleGoOnline() {
                let _self = this;
                this.$confirm({
                    title: '操作确认',
                    content: '要重新连接推送服务器吗？',
                    onOk() {
                        _self.init();
                    },
                    onCancel() {
                        console.log('Cancel');
                    },
                    class: 'test',
                });
            }
        },
        mounted() {
            // this.init();
        }
    }
</script>

