<template>
    <layout>
        <router-view></router-view>
    </layout>
</template>

<script>
    import layout from './Layout'

    export default {
        components: {
            layout
        },
        data() {
            return {}
        },
        created() {
            $("body").css("background-color","#fdf6e2");
            if (!this.$store.getters.getAccessToken) {
                this.$router.push({name: 'login'});
            }
        },
        watch: {//使用watch 监听$router的变化
            '$route'(to, from) {
                console.log(to, from)
            }
        }
    };
</script>

